import { useMemo, useRef } from 'react'
import { useThree, extend, useFrame } from '@react-three/fiber'
import { Particles } from './particles'
import { Stars } from './stars'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'

export const Main = () => {
  const { camera, gl } = useThree()

  const groupRef = useRef()
  const planetRef = useRef()
  const torusRef = useRef()

  // we use 'delta' here to get a more standarized framerate
  useFrame((_state, delta) => {
    groupRef.current.rotation.y += 0.04 * delta
    planetRef.current.rotation.y -= 0.08 * delta
    torusRef.current.rotation.y += 0.25 * delta
  })

  // we need to extend R3F to get acces to some external functions
  extend({ OrbitControls })

  return (
    <>
      <orbitControls args={[camera, gl.domElement]} />
      <directionalLight position={[1, 2, 3]} intensity={0.5} />
      <ambientLight intensity={0.5} />

      <Particles />
      <Stars />

      <group ref={groupRef}>
        <mesh ref={planetRef}>
          <sphereGeometry args={[3, 32, 32]} />
          <meshStandardMaterial color="white" wireframe={true} />
        </mesh>
        <mesh ref={torusRef}>
          <torusGeometry radius={10} />
          <meshStandardMaterial color="white" />
        </mesh>
      </group>
    </>
  )
}
